@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Satisfy&display=swap);
@import url(https://fonts.googleapis.com/css2?family=PT+Sans&display=swap);
body {
  margin: 0;
  font-family: "Poppins", -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.app{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-direction: column;
          flex-direction: column;
  padding: 40px 24px;
  background-color: rgba(238, 231, 231, 0.425);
}

.app__searchForm{
  margin-top: 18px;
}

.app__input{
  padding: 6px 16px;
  min-width: 200px;
  max-width: 100vw;
  font-size: 1rem;
  text-align: center;
  background: transparent;
  border: none;
  border-bottom: 2px solid #FFB03B;
  outline: none;
  color: #fff;
}

::-webkit-input-placeholder{
  color: #fff;
}

:-ms-input-placeholder{
  color: #fff;
}

::placeholder{
  color: #fff;
}

.app__submit{
  padding: 6px 16px;
  font-size: 15px;
  margin-left: 6px;
  background-color: #02aff0;
  border: 2px solid #02aff0;
  border-radius: 4px;
  color: white;
  cursor: pointer;
  transition: all 0.3s;
}

.app__submit:hover{
  border: 2px solid #02aff0;
  background-color: white;
  color: #02aff0;
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}

.app__recipes{
  margin-top: 25px;
  display: grid;
  grid-template-columns: 250px 250px 250px 250px;
  grid-gap: 50px;
  text-align: center;
}

.app__healthLabels{
  padding: 7px 8px;
  margin-left: 6px;
  border-radius: 4px;
  cursor: pointer;
}



.header{
  min-height: 96vh;
  width: 100%;
  background: url(/delicious/static/media/banner-bg.d51951a6.jpg) no-repeat center top;
}

.header2{
  min-height: 96vh;
  width: 100%;
  background: url(/delicious/static/media/indianfood.f18ea4cf.jpg) no-repeat center top;
  background-size: cover;
}

.overlay{
  min-height: 96vh;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-direction: column;
          flex-direction: column;
  margin-top: -10vh;
}

.logo, .footer__logo{
  font-family: 'Satisfy', sans-serif;
    font-size: 3.6rem;
    color: #FFB03B;
}


.overlay h2{
  font-family: 'Satisfy', sans-serif;
    font-size: 2.3rem;
    color: #FFB03B;
}

.overlay p{
  font-family: 'PT Sans', sans-serif;
    color: #fcfcfc;
    font-size: 1.6rem;
    width: 80%;
    margin: 1rem auto;
    text-align: center;
}

.footer {
  width: 100%;
  background-color: #222;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-direction: column;
          flex-direction: column;
  min-height: 27vh;
  padding: 1rem 0;
}

.footer__para{
  color: #999;
    text-transform: none;
    font-style: normal;
    padding-bottom: 15px;
}

.fa{
  color: white;
  margin: 0 0.4rem;
  font-size: 1.8rem;
}

@media only screen and (max-width:1100px)
{
  .app__recipes{
    grid-template-columns: 250px 250px 250px;
  } 
}

@media only screen and (max-width:870px)
{

  html{
    font-size: 14px;
  }

  .app__recipes{
    grid-template-columns: 250px 250px;
  } 

}

@media only screen and (max-width:562px)
{

  html{
    font-size: 10px;
  }

  .app__recipes{
    grid-template-columns: 250px;
  } 

  .app__searchForm{
    margin-top: 18px;
    height: 35vh;
    display: -webkit-flex;
    display: flex;
    -webkit-align-content: center;
            align-content: center;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-flex-direction: column;
            flex-direction: column;
  }

  .app__healthLabels{
    margin-left: 0px;
    margin-top: 5px;

  }

  .app__submit{
    margin-left: 0;
    margin-top: 5px;
  }
}

.recipeTile{
    margin: 1rem 0;
}

.recipeTile:hover{
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
}

.recipeTile__img{
    height: 200px;
    width: 200px;
    object-fit: cover;
    border-radius: 4px;
}

.recipeTile__name{
    max-width: 200px;
    margin: 8px auto;
}

a{
    text-decoration: none;
    color: black;
    
    margin-bottom: 1rem;
    transition: all 0.3s;

}
a:hover{
    -webkit-transform: scale(1.05);
            transform: scale(1.05);

}
.menu{
    height: 10vh;
    display: -webkit-flex;
    display: flex;
    max-width: 80%;
    margin: 0 auto;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}


.menu h1{
    font-family: 'Satisfy', sans-serif;
    color: #FFB03B;
    cursor: pointer;
}

.menu ul{
    list-style-type: none;
    display: -webkit-flex;
    display: flex;
    color: #fff;
}

.menu a{
    color: #fff;
}

.menu a:hover{
    
    color: #FFB03B;
}

.menu li{
    margin: 0 1rem;
    cursor: pointer;
    transition: all .5s;
}

.activenav{
    color: #FFB03B!important;
}

